import axios from 'axios';

const api = process.env.VUE_APP_API;

// 員工
export const apiAdminSignIn = (data) => axios.post(`${api}/admin/sign_in`, data);
export const apiGetWorkers = () => axios.get(`${api}/admin/workers`);
export const apiPostWorker = (data) => axios.post(`${api}/admin/worker`, data);
export const apiPatchWorker = (data) => axios.patch(`${api}/admin/worker`, data);
export const apiAdminDeleteWorker = (_id) => axios.delete(`${api}/admin/worker/${_id}`);
export const apiGetWorkerList = (id) => axios.get(`${api}/admin/workerList/${id}`);

// 廠商
export const apiGetCompanys = () => axios.get(`${api}/admin/companys`);
export const apiPostCompany = (data) => axios.post(`${api}/admin/company`, data);
export const apiPatchCompany = (data) => axios.patch(`${api}/admin/company`, data);
export const apiDeleteCompany = (_id) => axios.delete(`${api}/admin/company/${_id}`);
export const apiGetCooperateList = (id) => axios.get(`${api}/admin/cooperateList/${id}`);

// 工單
export const apiGetOrders = (data) => axios.get(`${api}/admin/orders`, { params: data });
export const apiGetOrder = (id) => axios.get(`${api}/admin/order/${id}`);
export const apiPostOrder = (data) => axios.post(`${api}/admin/order`, data);
export const apiPatchOrderStatus = (data) => axios.patch(`${api}/admin/orderStatus`, data);
export const apiPatchOrder = (data) => axios.patch(`${api}/admin/order`, data);
export const apiDeleteOrder = (id) => axios.delete(`${api}/admin/order/${id}`);

// admin
// export const apiSignUp = (data) => axios.post(`${api}/admin/sign_up`, data);
export const apiSignIn = (data) => axios.post(`${api}/admin/sign_in`, data);
